import { likeBlog, removeBlog } from '../reducers/blogReducer'
import { useDispatch } from 'react-redux'
import { showNotification } from '../reducers/notificationReducer'
import { useParams } from 'react-router-dom'
import CommentForm from './CommentForm'
import Comments from './Comments'

const Blog = ({ blogs }) => {
  const dispatch = useDispatch()

  const id = useParams().id
  const blog = blogs.find(b => b.id === id)

  const handleLike = blog => {
    dispatch(likeBlog(blog))
    dispatch(showNotification(`You liked blog "${blog.title}".`, 5))
  }

  const handleRemove = blog => {
    dispatch(removeBlog(blog))
    dispatch(showNotification(`Deleted a blog called ${blog.title}`, 5))
  }

  const handleNewComment = async newComment => {
    if (!newComment.comment) {
      dispatch(showNotification('No comment written', 5))
    }
    //dispatch(createComment(blog, newComment))
    console.log('implement add comment')
    dispatch(showNotification(`Comment ${newComment.comment} added`, 5))
  }

  if (!blog) return null

  return (
    <div id="blog" className="blog">
      <h3>
        {' '}
        {blog.title} by {blog.author}{' '}
      </h3>
      <div className="blogInfo">
        <a href={blog.url}>{blog.url}</a>
        <p> {blog.likes} people have liked this blog</p>
        <p> added by {blog.user.name} </p>
        <button
          id="like-button"
          className="like-button"
          type="button"
          onClick={() => handleLike(blog)}
        >
          like
        </button>
        <button
          id="remove-button"
          className="remove-button"
          type="button"
          onClick={() => handleRemove(blog)}
        >
          remove
        </button>
        <h3>Comments</h3>
        <CommentForm handleNewComment={handleNewComment} />
        <Comments />
      </div>
    </div>
  )
}

export default Blog
